/* Experience.css - Matched to the modern, "squishy" theme */

/* Using the same variables from your main theme for consistency */
:root {
  --primary-color: #6a5acd;
  --secondary-color: #f0f2f5;
  --background-color: #f8f9fa;
  --card-background: #ffffff;
  --text-color: #555;
  --heading-color: #2c3e50;
  --shadow-color: rgba(106, 90, 205, 0.15);
}

.experience-section {
  max-width: 900px;
  margin: 60px auto;
  padding: 40px 20px;
  font-family: 'Poppins', sans-serif; /* Switched to Poppins font */
  color: var(--text-color);
  /* The section itself doesn't need a background if the page has one */
}

.experience-section h2 {
  font-size: 2.8rem;
  font-weight: 700;
  color: var(--heading-color);
  text-align: center;
  margin-bottom: 50px;
  position: relative;
}

/* Underline effect for the main header */
.experience-section h2::after {
  content: '';
  display: block;
  width: 80px;
  height: 4px;
  background-color: var(--primary-color);
  border-radius: 2px;
  margin: 10px auto 0;
}

/* Re-styled to be a floating card, removing the timeline style */
.experience-item {
  background: var(--card-background);
  border-radius: 20px; /* Softer, more rounded corners */
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 10px 30px var(--shadow-color);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: none; /* Removed the old border-left */
  position: relative;
}

.experience-item:hover {
  transform: translateY(-10px); /* Lift effect on hover */
  box-shadow: 0 20px 40px rgba(106, 90, 205, 0.25);
}

.experience-header {
  display: flex;
  align-items: center;
  gap: 20px; /* Space between logo and text */
  margin-bottom: 20px;
  flex-wrap: wrap; /* Allow wrapping on small screens */
}

.company-logo {
  width: 60px;
  height: 60px;
  object-fit: contain;
  border-radius: 50%; /* Circular logo to match your profile image */
  border: 3px solid var(--primary-color);
  padding: 5px; /* Adds a bit of space around the logo */
  background-color: white;
}

.job-details {
  flex-grow: 1;
  text-align: center;
}

.job-title {
  font-size: 1.5em;
  font-weight: 600;
  color: var(--heading-color);
  margin: 0 0 4px 0;
}

.company-name {
  font-size: 1.1em;
  font-weight: 500;
  color: var(--primary-color);
  margin: 0;
}

.job-location-dates {
  font-size: 0.9em;
  color: #777;
  margin-top: 8px;
  font-style: normal;
  text-align: right;
}

.responsibilities-list {
  list-style: disc; /* Use standard disc bullet points */
  padding-left: 20px; /* Standard indentation for a bulleted list */
  margin-top: 15px;
}

.responsibilities-list li {
  font-size: 1em;
  line-height: 1.8;
  color: var(--text-color);
  margin-bottom: 12px;
  padding-left: 5px; /* Adjust left padding for standard bullets */
  position: relative;
}

.responsibilities-list li:hover::before {
  transform: translateX(5px); /* Moves the arrow slightly to the right on hover */
}

.experience-item:last-child {
  margin-bottom: 0;
}