/* src/components/ProjectCard.css */

/* Using the same variables from your main theme */
:root {
  --primary-color: #6a5acd;
  --secondary-color: #f0f2f5;
  --card-background: #ffffff;
  --text-color: #555;
  --heading-color: #2c3e50;
  --shadow-color: rgba(106, 90, 205, 0.15);
}

/* --- Main Card Styling --- */
.project-card {
  font-family: 'Poppins', sans-serif; /* Use Poppins font for the card */
  background: var(--card-background);
  border-radius: 20px; /* Softer, more rounded corners */
  overflow: hidden;
  box-shadow: 0 10px 30px var(--shadow-color);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%; /* Ensures cards in a grid have the same height */
  border: none; /* Removed the hard border */
}

.project-card:hover {
  transform: translateY(-10px); /* A more pronounced "lift" effect */
  box-shadow: 0 20px 40px rgba(106, 90, 205, 0.25);
}

/* --- Card Image --- */
.project-image {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-bottom: none; /* Removed the hard separator line */
}

/* --- Card Content --- */
.project-content {
  padding: 25px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.project-title {
  margin: 0 0 10px 0;
  font-size: 1.6em;
  font-weight: 600;
  color: var(--heading-color);
}

.project-description {
  font-size: 1em;
  color: var(--text-color);
  line-height: 1.6;
  flex-grow: 1;
  margin-bottom: 20px;
}

/* --- Technology Tags (Pill Style) --- */
.project-technologies {
  margin-bottom: 20px;
}

.project-technologies ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tech-tag {
  background-color: var(--secondary-color);
  color: var(--heading-color);
  padding: 8px 16px;
  border-radius: 30px; /* Pill shape to match skill tags */
  font-size: 0.85em;
  font-weight: 500;
  transition: all 0.3s ease;
}

.tech-tag:hover {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-3px);
}

/* --- Links (Pill Buttons) --- */
.project-links {
  margin-top: auto;
  padding-top: 15px;
  border-top: 1px solid #f0f2f5; /* A softer separator */
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.project-link {
  text-decoration: none;
  font-weight: 600;
  padding: 10px 22px;
  border-radius: 30px; /* Pill shape */
  transition: all 0.3s ease-in-out;
  font-size: 0.9em;
  text-align: center;
  display: inline-flex; /* To align icon and text */
  align-items: center;
  gap: 8px; /* Space between icon and text */
}

/* Primary Button Style */
.live-link {
  background-color: var(--primary-color);
  color: white;
  border: 2px solid var(--primary-color);
}

.live-link:hover {
  background-color: #5849b6; /* Slightly darker primary color */
  border-color: #5849b6;
  transform: scale(1.05);
}

/* Secondary/Outline Button Style */
.repo-link {
  background-color: transparent;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

.repo-link:hover {
  background-color: var(--primary-color);
  color: white;
  transform: scale(1.05);
}

/* Responsive adjustments */
@media (max-width: 400px) {
  .project-links {
    flex-direction: column;
    gap: 10px;
  }
  .project-link {
    width: 100%;
    box-sizing: border-box;
  }
}