/* --- CSS Variables for Easy Customization --- */
:root {
  --primary-color: #6a5acd; /* A modern slate blue/purple */
  --secondary-color: #f0f2f5;
  --background-color: #f8f9fa;
  --card-background: #ffffff;
  --text-color: #555;
  --heading-color: #2c3e50;
  --shadow-color: rgba(106, 90, 205, 0.15);
}

/* --- Main Section Styling --- */
.about-me-section {
  font-family: 'Poppins', sans-serif;
  background-color: transparent;
  padding: 80px 20px;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* --- Main Content Card --- */
.about-me-content {
  /* This now stacks its children (intro and skills) vertically */
  display: flex;
  flex-direction: column; 
  gap: 2rem; /* Adds space between the intro and skills sections */
  max-width: 1100px;
  margin: 0 auto;
  background: var(--card-background);
  padding: 50px;
  border-radius: 25px;
  box-shadow: 0 15px 40px var(--shadow-color);
}

/* --- NEW: Intro Section (Text + Image) --- */
.about-me-intro {
  display: flex;
  align-items: flex-start; /* Aligns top of text with top of image */
  gap: 3rem;
  flex-wrap: wrap; /* Allows image to go below text on small screens */
}

/* --- Squishy Headshot Image (no changes needed) --- */
.about-me-img {
  flex: 1;
  max-width: 280px;
  min-width: 240px;
  border-radius: 50%;
  border: 6px solid var(--primary-color);
  box-shadow: 0 8px 25px var(--shadow-color);
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275), box-shadow 0.3s ease;
}

.about-me-img:hover {
  transform: scale(1.07) rotate(4deg);
  box-shadow: 0 12px 35px rgba(106, 90, 205, 0.3);
}

/* --- Text Styling (was .about-me-text) --- */
.about-me-intro-text {
  flex: 2; /* Takes up more space than the image */
  min-width: 300px;
}

.about-me-intro-text h2 {
  font-size: 2.8rem;
  font-weight: 700;
  color: var(--heading-color);
  margin-bottom: 1rem;
}

.about-me-intro-text p {
  color: var(--text-color);
  line-height: 1.7;
  margin-bottom: 1.5rem;
}

/* --- Skills and Links Styling --- */
.skills-header {
  margin-top: 1rem;
  color: var(--heading-color);
  font-weight: 600;
}

.skill-category {
  margin-bottom: 1.5rem;
}

.skill-category h4 {
  margin-bottom: 1rem;
  color: var(--heading-color);
  border-bottom: 2px solid var(--primary-color);
  padding-bottom: 0.5rem;
  display: inline-block;
}

.skill-category ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.skill-category li {
  background-color: var(--secondary-color);
  color: var(--heading-color);
  padding: 10px 20px;
  border-radius: 30px;
  font-size: 0.9rem;
  font-weight: 500;
  box-shadow: 0 2px 5px var(--shadow-color);
  transition: all 0.3s ease;
  display: flex; /* Make it a flex container */
  align-items: center; /* Vertically align icon and text */
  gap: 8px; /* Space between icon and text */
}
.skill-category li i {
  /* color: var(--primary-color); Give icons an accent color */
  font-size: 1.1em; /* Make icons slightly larger than text */
}

.skill-category li:hover {
  /* background-color: var(--primary-color); */
  color: black;
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 8px 15px var(--shadow-color);
}

/* --- Social Links --- */
.social-links {
  margin-top: 2.5rem;
}


.social-links a {
  text-decoration: none;
  color: var(--primary-color);
  font-weight: 600;
  padding: 12px 24px;
  border: 2px solid var(--primary-color);
  border-radius: 30px;
  margin-right: 10px;
  transition: all 0.3;
  display: inline-flex; /* Use inline-flex for buttons with icons */
  align-items: center;
  gap: 8px; /* Space between icon and text in buttons */
}

.social-links a:hover {
  text-decoration: none;
  color: var(--primary-color);
  font-weight: 600;
  padding: 12px 24px;
  border: 2px solid var(--primary-color);
  border-radius: 70px;
  margin-right: 10px;
  transition: all 0.3;
  display: inline-flex; /* Use inline-flex for buttons with icons */
  align-items: center;
  gap: 8px; /* Space between icon and text in buttons */
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 8px 15px var(--shadow-color);
}
.social-links a i {
    font-size: 1.2em; /* Slightly larger social icons */
}
.skills-and-links {
  display: flex;
  flex-direction: column;
  align-items: center;
}
